/* global document, Office */

Office.onReady((info) => {
  if (info.host === Office.HostType.Outlook) {
    document.getElementById("sideload-msg").style.display = "none";
    document.getElementById("app-body").style.display = "flex";
    document.getElementById("run").onclick = run;
  }
});

// Funkcia na overenie domény cez server
async function checkDomainOnServer(domain: string): Promise<boolean> {
  try {
    const response = await fetch(`https://timovy-projekt.polandcentral.cloudapp.azure.com/api/api/verify/${domain}`);
    if (response.status === 200) {
      return true;
    } else if (response.status === 404) {
      return false;
    } else {
      console.error(`Unexpected response status: ${response.status}`);
      return false;
    }
  } catch (error) {
    console.error('Error while checking domain on server:', error);
    return false;
  }
}

export async function run() {
  const item = Office.context.mailbox.item;

  const senderEmail = item.from?.emailAddress || "Unknown";
  const senderDomain = senderEmail.split("@")[1];

  // Overenie domény cez server
  const isAuthorized = await checkDomainOnServer(senderDomain);

  // Aktualizácia UI podľa výsledku
  document.getElementById("item-subject").innerHTML = `
    <b>Subject:</b> <br/> ${item.subject} <br/>
    <b>Sender:</b> ${senderEmail} <br/>
    <b>Status:</b> ${isAuthorized ? "Authorized" : "Unauthorized"}
  `;

  const statusElement = document.getElementById("current-status");
  if (statusElement) {
    statusElement.innerText = isAuthorized ? "Authorized" : "Unauthorized";
    statusElement.className = isAuthorized ? "authorized" : "unauthorized";
    statusElement.style.color = isAuthorized ? "green" : "#c60101";
    const appBody = document.getElementById("app-body");
    if (appBody) {
      if (isAuthorized) {
        appBody.style.backgroundColor = "#35fb0d"; // Svetlozelené pozadie
      } else {
        appBody.style.backgroundColor = "#ff6a37"; // Svetločervené pozadie
      }
    }
  }

  if (!isAuthorized) {
    const alertBox = document.getElementById("alert-box");
    if (alertBox) {
      alertBox.style.display = "block";
    }
  }

  // Log do konzoly pre debug
  console.log(`Checked domain: ${senderDomain}, Authorized: ${isAuthorized}`);
}